import React from 'react'

import { Web3ReactProvider, useWeb3React } from '@web3-react/core'
import { Web3Provider } from '@ethersproject/providers'
import { formatEther } from '@ethersproject/units'
import { BigNumberish } from "@ethersproject/bignumber"
import { Contract } from '@ethersproject/contracts'

// import * as Infura from './infura'
import * as Connectors from './connectors'
import { useEagerConnect } from './hooks'
import { TokenId, getEIP1155Contract, RaribleToken } from './nft'

function getLibrary(provider: any): Web3Provider {
  const library = new Web3Provider(provider)
  return library
}

function AppWrapper() {
  return (
    <Web3ReactProvider getLibrary={getLibrary}>
      <App/>
    </Web3ReactProvider>
  )
}

function Balance() {
  const { account, library, chainId } = useWeb3React<Web3Provider>()
  const [balance, setBalance] = React.useState<BigNumberish>(0)
  React.useEffect(() => {
    if (!account || !library) { return }
    
    (async () => {
      const balance = await library.getBalance(account)
      setBalance(balance)
    })()
  }, [account, library, chainId])

  return (<>üí∞ Œû{formatEther(balance)}</>)
}

const EIP1155Contract: React.FC<{tokenId: string}> = ({children, tokenId}) => {
  const { account, library, chainId } = useWeb3React<Web3Provider>()

  const [contract, setContract] = React.useState<Contract | undefined>(undefined)
  const [balance, setBalance] = React.useState<BigNumberish>(0)
  const [raribleToken, setRaribleToken] = React.useState<RaribleToken>()
  
  React.useEffect(() => {
    if (!account || !library) { return }
    (async () => {
      const signer = await library.getSigner(account)
      const contract = getEIP1155Contract(signer)
      setContract(contract)

      const balance = await (contract as any).balanceOf(account, tokenId)
      setBalance(balance)

      const tokenUri = await (contract as any).uri(tokenId)
      const raribleToken = await (await fetch(tokenUri)).json() as RaribleToken
      setRaribleToken(raribleToken)
    })()
  }, [account, library, chainId])

  return (
    <>
      <p>Contract address {contract?.address}</p>
      <p>Token Id {tokenId}</p>
      <p>Name {raribleToken?.name}</p>
      <p>Description {raribleToken?.description}</p>
      <p>External URL {raribleToken?.external_url}</p>
      <p>üí∞ {balance.toString()}</p>
    </>)
}

function App() {
  const context = useWeb3React<Web3Provider>()
  const { connector, chainId, account, activate, deactivate, active, error } = context

  useEagerConnect()

  const connected = connector === Connectors.injected

  return (
    <>
      <h1>Step 1. Connect to Etherium</h1>
      <button onClick={() => { activate(Connectors.injected) }}>Metamask</button>
      <button onClick={() => { activate(Connectors.ledger) }}>Ledger</button>
      <button onClick={() => { activate(Connectors.trezor) }}>Trezor</button>
      <button onClick={() => { activate(Connectors.walletconnect) }}>Walletconnect</button>
      <button onClick={() => { deactivate() }}>Disconnect</button>
      <br/>
      { active && <>üèÉ‚Äç‚ôÇÔ∏è Active</> }
      { connected && <>‚úÖ Connected</> }
      { error && <>‚ö†Ô∏è {error.message}</> }
      { chainId && <>üï∏Ô∏è {chainId}</> }
      { account && <>üßç {account}</> }
      { connected && <Balance/> }

      <br/>
      <h1>Step 2. NFT</h1>
      <a href="https://etherscan.io/address/0xd07dc4262bcdbf85190c01c996b4c06a461d2430" target="_blank">Go to contract on etherscan.io</a>
      <br/>
      <EIP1155Contract tokenId={TokenId}/>
    </>)
      
}

export default AppWrapper
